








































































import {Component, Vue} from 'vue-property-decorator';
import {trace_web} from '@/api';
import _ from 'lodash';

@Component({
    name: 'Index',
    components: {}
})
export default class Index extends Vue {
    tasksStatistics: any = {};

    query: any = {
        pageNum: 1,
        pageSize: 10,
    }

    loading: boolean = false

    list: any = {
        data: [],
        total: 0
    }

    columns: any = [
        {
            title: '商品批次编号',
            key: 'batchNumber',
            minWidth: 200,
        },
        {
            title: '商品名称',
            key: 'commodityName',
            minWidth: 120,
        },
        {
            title: '客户名称',
            key: 'customerName',
            minWidth: 90,
        },
        {
            title: '合同名称',
            key: 'contractName',
            minWidth: 130,
        },
        {
            title: '出/入库',
            key: 'type',
            minWidth: 100,
            dataType: 'enum',
            filters: [{text: '出库', value: 'out'}, {text: '入库', value: 'in'},],
            columnKey: 'type'
        },
        {
            title: '单号',
            key: 'orderNumber',
            minWidth: 200,
        },
        {
            title: '追溯码数量',
            key: 'traceCodeNumber',
            minWidth: 130,
        },
        {
            title: '生成时间',
            key: 'createTime',
            minWidth: 160,
        },
        {
            title: '操作',
            key: 'option',
            minWidth: 100,
            fixed: 'right',
            render: (h: Vue.CreateElement, params: any) => {
                let btn = [];
                if (this.$SSO_AUC('traceCodeDetail')){
                btn.push(
                    h('span', {
                        domProps: {
                            innerHTML: '追溯码详情'
                        },
                        on: {
                            click: () => {
                                this.toView(params.row);
                            }
                        }
                    })
                )
                }
                return h('div', {
                    class: 'handleBtn'
                }, btn)
            }
        }
    ];

    created() {
        this.getCount();
        this.queryList();
    }

    getCount() {
        trace_web.trace_web_traceCodeManager_count_post({
            data: {}
        }).then((data: any) => {
            this.tasksStatistics = data.data || {};
        })
    }

    queryList(pageNum?: number) {
        this.loading = true;
        this.query.pageNum = pageNum || 1;
        trace_web.trace_web_traceCodeManager_query_post({
            data: {
                ...this.query
            }
        }).then((data: any) => {
            this.list.data = data.data || [];
            this.list.total = data.total;
        }).finally(() => {
            this.loading = false;
        })
    }

    // 表头条件筛选框
    filterChange(value: any, row: any) {
        _.forOwn(value, (res, key) => {
            this.query[key] = res[0];
            this.queryList(1);
        })
    }

    toView(row: any) {
        console.log(row)
        this.$router.push({
            path: 'traceCodeManage/traceCodeInfo',
            query: {
                // traceCode: row.traceCodeNumber,
                batchNumber: row.batchNumber,
                id: row.id
            }
        })
    }

    onReset() {
        this.query = {
            pageNum: 1,
            pageSize: 10,
        }
    }

    // 分页切换
    sizeChange(e: any) {
        this.query.pageSize = e;
    }

}
